import { connect } from 'react-redux'
import { reduxForm, formValueSelector, change, getFormValues } from 'redux-form'
import { withRouter } from 'react-router-dom'
import CompleteProfilePreferencesForm from 'Candidate/forms/CompleteProfile/Preferences'
import validate from './CompleteProfilePreferencesFormValidator'
import { scrollToFirstError } from 'Shared/helpers/scroll'
import { fetchCities, fetchCurrenciesIfNeeded } from 'Shared/ducks/resources'
import { fetchIndustries } from 'Shared/ducks/industries'
import { fetchDepartments, fetchCandidateDepartments } from 'Shared/ducks/departments'
import { fetchCandidateLocations } from 'Shared/ducks/locations'
import { getAllCities } from 'Shared/selectors/cities'
import { transformToArray } from 'Shared/helpers/redux'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { getCurrenciesAsOptions, getCurrency } from 'Shared/selectors/resources'
import { omit, path } from 'ramda'
import { getLocationCurrencyId } from 'Shared/selectors/geolocation'
import { updateUserProfilePreferences } from 'Shared/ducks/user/candidate'
import { isFieldInvalid, getFieldErrorMessage } from 'Shared/selectors/reduxForm'
import { getUserPreferredLocationsList } from 'Shared/selectors/locations'
import { prepareSalaryRangeValue, getSalaryRangeValue } from 'Shared/helpers/salary'
import { SALARY_RANGE } from 'Shared/constants/salaryRange'

const formUtilsService = new FormUtilsService()
const mapOptionsByLanguage = formUtilsService.mapOptionsByLanguageTranslations

export const formName = 'CompleteProfile-Preferences'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmitFail: scrollToFirstError,
  enableReinitialize: true,
  onSubmit: async (values, dispatch) => {
    const newValues = {
      ...omit(['salary', 'jobType', 'graduateWage'], values),
      salaryRange: prepareSalaryRangeValue(values)
    }
    return dispatch(updateUserProfilePreferences(newValues))
  }
})(CompleteProfilePreferencesForm)

const mapStateToProps = state => {
  const userDetails = path(['user', 'details'], state)
  const selectedCurrencyId = formValueSelector(formName)(state, 'currencyId')

  return {
    initialValues: {
      industries: mapOptionsByLanguage(transformToArray(state.industries.candidate)),
      departments: mapOptionsByLanguage(transformToArray(state.departments.candidate), 'description'),
      preferredLocations: getUserPreferredLocationsList(state),
      interestedInInternship: userDetails.interestedInInternship || false,
      interestedInGraduateJob: userDetails.interestedInGraduateJob || false,
      interestedInVolunteering: userDetails.interestedInVolunteering || false,
      interestedInEventPromotion: userDetails.interestedInEventPromotion || false,
      hasSalaryExpectations: true,
      graduateWage: userDetails.salaryRange || SALARY_RANGE.graduateWage1,
      salaryRange: getSalaryRangeValue(userDetails.salaryRange) || SALARY_RANGE.notSure,
      currencyId: userDetails.currencyId || getLocationCurrencyId(state)
    },
    salaryValue: formValueSelector(formName)(state, 'salary') || { min: 0, max: 0 },
    selectedCurrency: getCurrency(state, selectedCurrencyId),
    cities: getAllCities()(state),
    industries: mapOptionsByLanguage(transformToArray(state.industries.entries)),
    departments: mapOptionsByLanguage(transformToArray(state.departments.entries), 'description'),
    currencies: getCurrenciesAsOptions(state),
    hasJobTypeError: isFieldInvalid(formName, 'jobType')(state),
    errorMessage: getFieldErrorMessage(formName, 'jobType')(state),
    selectedValues: getFormValues(formName)(state)
  }
}

const mapDispatchToProps = dispatch => ({
  onMinSalaryChange: salaryMin => dispatch(change(formName, 'salaryFrom', salaryMin)),
  onMaxSalaryChange: salaryMax => dispatch(change(formName, 'salaryTo', salaryMax)),
  fetchCandidateDepartments: () => dispatch(fetchCandidateDepartments()),
  fetchCandidateLocations: () => dispatch(fetchCandidateLocations()),
  fetchIndustries: () => dispatch(fetchIndustries()),
  fetchDepartments: () => dispatch(fetchDepartments()),
  fetchCities: () => dispatch(fetchCities()),
  fetchCurrencies: () => dispatch(fetchCurrenciesIfNeeded())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



