import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import { getUserId } from 'Shared/selectors/user'
import { filter, pipe, propEq, map, propOr, isEmpty, not } from 'ramda'

const getLocations = state => state.locations.candidate

export const getUserPreferredLocationsList = createSelector(
  getLocations,
  getUserId,
  (locations, userId) => pipe(
    transformToArray,
    filter(
      propEq('candidateId', userId)
    ),
    map(
      propOr('', 'city')
    ),
    filter(
      pipe(
        isEmpty,
        not
      )
    )
  )(locations)
)



