import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import TeamMembersAcceptInvitationView from 'Employer/views/TeamMembers/TeamMembersAcceptInvitationView'
import { getOrganisationInviteDetails } from 'Shared/ducks/organisationInvites'
import { getOrganisationInviteById } from 'Shared/selectors/organisationInvites'

const mapStateToProps = (state, ownProps) => ({
  organisationInviteDetails: getOrganisationInviteById(state, ownProps.match.params.inviteId)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  getOrganisationInviteDetails: () => dispatch(getOrganisationInviteDetails(ownProps.match.params.inviteId))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TeamMembersAcceptInvitationView))



