import React from 'react'
import CompleteProfilePreferencesFormMobile from './CompleteProfilePreferencesForm.mobile'
import CompleteProfilePreferencesFormWeb from './CompleteProfilePreferencesForm'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CompleteProfilePreferencesForm = props => props.isMobile ? <CompleteProfilePreferencesFormMobile {...props} /> : <CompleteProfilePreferencesFormWeb {...props} />

export default withDeviceWidthDetector(CompleteProfilePreferencesForm)



