import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Container from 'Shared/components/Container/index'
import BackButton from 'Shared/components/BackButton/BackButton'
import { t } from 'i18next'
import './CareerPreferencesView.scss'
import { PublicRoutes } from 'Shared/constants/Routes'
import { PrimaryButton } from 'Shared/components/Button'
import CompleteProfilePreferencesFormConnector from 'Candidate/connectors/Forms/CompleteProfile/Preferences/CompleteProfilePreferencesFormConnector'
import NotificationService from 'Shared/services/NotificationService'
import { Roles } from 'Shared/constants/User'

function CareerPreferencesView (props) {
  const [isSubmitting, setIsSubmitting] = useState(false)

  const goBack = () => props.history.push(PublicRoutes.jobs)

  useEffect(() => {
    if (props.userRole !== Roles.candidate.name) {
      props.history.push('/not-found')
    }

    if (props.submitCareerPreferencesSucceeded) {
      setIsSubmitting(false)
      return NotificationService.success(t('alerts.updateCareerPreferencesSuccess'))
    }

    if (props.submitCareerPreferencesFailed) {
      setIsSubmitting(false)
      return NotificationService.error(t('alerts.updateCareerPreferencesFailed'))
    }
  })

  const handleSubmit = () => {
    setIsSubmitting(true)
    return props.submitCareerPreferences()
  }

  return (
    <Container>
      <div className='career-preferences__heading'>
        <BackButton button onClick={goBack} name={t('common.back')} />
        <h1 className='career-preferences__heading-title'>{t('common.careerPreferences')}</h1>
      </div>
      <div className='career-preferences__form-container'>
        <h2 className='career-preferences__form-header'>{t('candidate.profile.careerPreferences.title')}</h2>
        <div>
          <CompleteProfilePreferencesFormConnector />
        </div>
        <div className='career-preferences__form-submit'>
          <PrimaryButton disabled={isSubmitting} onClick={handleSubmit}>{isSubmitting ? `${t('common.loading')}...` : t('common.saveChanges')}</PrimaryButton>
        </div>
      </div>
    </Container>
  )
}

export default withRouter(CareerPreferencesView)



