import React from 'react'
import { Form, Field } from 'redux-form'
import Title from 'Shared/components/FormElements/Title'
import { Trans } from 'react-i18next'
import { Link, withRouter } from 'react-router-dom'
import { InputWithPreview, Input, InputWithIcon, CheckboxWithErrorLabel } from 'Shared/components/FormElements'
import { PrimaryBlockButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import { withPossessiveS, cleanseString } from 'Shared/helpers/string'
import './TeamMembersAcceptInvitationForm.scss'
import cx from 'classnames'
import { isErrorInFieldFromProps } from 'Shared/helpers/forms'

const TeamMembersAcceptInvitationForm = (props) => {
  const { error, submitting, t, handleSubmit, organisationInviteDetails } = props
  const organisationName = withPossessiveS(organisationInviteDetails.organisationName || '')

  const errorLabelClass = cx({
    'label-error__first-name-or-last-name': isErrorInFieldFromProps('lastName')(props) || isErrorInFieldFromProps('firstName')(props)
  })
  return (
    <Form
      className='complete-registration'
      onSubmit={handleSubmit}
    >
      <Title text={cleanseString(t('authorization.joinOrganisationAccount', { organisationName }))} />
      <p className='sign-up__description'>{t('authorization.joinOrganisationAccountDescription')}</p>
      <Field
        component={Input}
        label={t('forms.companyName.label')}
        name='organisationName'
        placeholder={t('forms.companyName.placeholder')}
        type='text'
        autoComplete='off'
        disabled
      />
      <div className='columns form__wrapper--inline'>
        <div className='column col-6'>
          <Field
            component={Input}
            labelClassName={errorLabelClass}
            label={t('forms.firstName.label')}
            name='firstName'
            autoComplete='off'
            placeholder={t('forms.firstName.placeholder')}
            type='text'
          />
        </div>
        <div className='column col-6'>
          <Field
            component={Input}
            labelClassName={errorLabelClass}
            label={t('forms.lastName.label')}
            name='lastName'
            placeholder={t('forms.lastName.placeholder')}
            type='text'
            autoComplete='off'
          />
        </div>
      </div>
      <Field
        component={InputWithIcon}
        iconClassName='ion-md-mail'
        label={t('forms.email.label')}
        name='email'
        placeholder={t('forms.email.placeholder')}
        type='text'
        autoComplete='username'
        disabled
      />
      <Field
        component={InputWithPreview}
        iconClassName='ion-md-lock'
        label={t('forms.password.label')}
        name='password'
        placeholder={t('forms.password.placeholder')}
        type='password'
        autoComplete='password'
      />
      <div>
        <h3 className='sign-up__subtitle'>{t('terms.privacyNotice.title')}</h3>
        <Trans i18nKey='terms.privacyNotice.description' className='sign-up__text' parent='div'>
          At Oliv, we take the privacy of our users very seriously. Our <Link to='/privacy' target='_blank'><strong>privacy
          policy</strong></Link> has been updated in accordance with the EU's General Data Protection Regulation (GDPR). Please
          read through it carefully and accept our terms only if you agree.
        </Trans>
      </div>
      <Field
        component={CheckboxWithErrorLabel}
        text={<Trans i18nKey='terms.privacyNotice.agree' parent='span'>
          I agree to the <Link to='/terms' target='_blank'><strong>terms and conditions</strong></Link>.
        </Trans>}
        name='hasAcceptedTermsAndPolicies'
      />
      <PrimaryBlockButton
        className='button--submit'
        disabled={error || submitting}
        onClick={props.onClick}
        type='submit'
        block
      >
        {t('authorization.joinInternsme')}
      </PrimaryBlockButton>
      <p className='team-member-registration__link-text'>{t('authorization.alreadyHaveAccount')} <Link
        to={'/employer/login'}>{t('authorization.login')}</Link></p>
    </Form>
  )
}

export default withRouter(withTranslations(TeamMembersAcceptInvitationForm))



