import { getFormMeta, getFormSyncErrors, reduxForm } from 'redux-form'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import TeamMembersAcceptInvitationForm from 'Employer/forms/TeamMembers/TeamMembersAcceptInvitationForm'
import validate from 'Employer/connectors/Forms/TeamMembers/TeamMembersAcceptInvitationFormValidator'
import { getOrganisationInviteById } from 'Shared/selectors/organisationInvites'
import { registerOrganisationManager } from 'Shared/ducks/user'
import { scrollToFirstError } from 'Shared/helpers/scroll'

export const formName = 'TeamMemberAcceptInvitation'

let Form = reduxForm({
  form: formName,
  onSubmitFail: scrollToFirstError,
  validate,
  enableReinitialize: true,
  onSubmit: async (values, dispatch, props) => {
    const { firstName, lastName, password } = values
    await dispatch(registerOrganisationManager(props.organisationInviteDetails.id, { firstName, lastName, password }))
    props.history.push('/employer/thanks')
  }
})(TeamMembersAcceptInvitationForm)

const mapStateToProps = (state, ownProps) => {
  const organisationInviteDetails = getOrganisationInviteById(state, ownProps.match.params.inviteId)

  return ({
    organisationInviteDetails,
    initialValues: {
      organisationName: organisationInviteDetails.organisationName,
      email: organisationInviteDetails.email
    },
    formErrors: getFormSyncErrors(formName)(state),
    formMeta: getFormMeta(formName)(state)
  })
}

export default withRouter(connect(mapStateToProps)(Form))



