import React, { Component } from 'react'
import TeamMembersAcceptInvitationFormConnector from 'Employer/connectors/Forms/TeamMembers/TeamMembersAcceptInvitationFormConnector'
import { segmentSpec } from 'TrackingScripts/scripts/employer/segment'
import * as R from 'ramda'
import withScripts from 'TrackingScripts'

class TeamMembersAcceptInvitationView extends Component {
  async componentDidMount () {
    await this.props.getOrganisationInviteDetails()
    const isFromEmailLink = R.pipe(
      R.pathOr(false, ['location', 'state', 'fromSignUpPage']),
      R.not
    )(this.props.history)

    if (isFromEmailLink) {
      segmentSpec.startSignUp(this.props.organisationInviteDetails.email)
    }
  }

  render () {
    return (
      <div className='form--authorization'>
        <TeamMembersAcceptInvitationFormConnector />
      </div>
    )
  }
}

export default withScripts(TeamMembersAcceptInvitationView)



