import * as R from 'ramda'
import { t } from 'i18next'

export default values => {
  const errors = {}

  const jobTypeFields = [
    'interestedInInternship',
    'interestedInGraduateJob',
    'interestedInVolunteering',
    'interestedInEventPromotion'
  ]

  if (R.isEmpty(values['departments'])) {
    errors['departments'] = {
      key: 'errors.requiredField',
      options: {
        field: t('forms.preferredDepartment.label')
      }
    }
  }

  if (R.isEmpty(values['industries'])) {
    errors['industries'] = {
      key: 'errors.requiredField',
      options: {
        field: t('forms.preferredIndustry.label')
      }
    }
  }

  if (R.isEmpty(values['preferredLocations'])) {
    errors['preferredLocations'] = {
      key: 'errors.requiredField',
      options: {
        field: t('forms.preferredLocation.label')
      }
    }
  }

  const isAnyJobTypeSelected = R.pipe(
    R.map(field => values[field]),
    R.all(R.equals(false)),
    R.not
  )(jobTypeFields)

  if (!isAnyJobTypeSelected) {
    errors['jobType'] = {
      key: 'errors.atLeastOneJobType'
    }
  }

  return errors
}



