import React, { Component } from 'react'
import { Form, Field } from 'redux-form'
import {
  RadioWrapper,
  Checkbox,
  MultiSelect,
  Select,
  Input,
  GeosuggestMultiSelect
} from 'Shared/components/FormElements'
import withTranslations from 'Shared/hoc/withTranslations'
import './CompleteProfilePreferencesForm.scss'
import { MAX_JOB_PREFERENCES_COUNT } from 'Shared/constants/JobPreferences'
import SalaryExpectationFields from 'Candidate/connectors/Forms/SalaryExpectationFormFields/SalaryExpectationFormFieldsConnetor'
import Label from 'Shared/components/FormElements/Label'

class CompleteProfilePreferencesForm extends Component {
  componentWillReceiveProps (nextProps) {
    const { salaryValue, onMinSalaryChange, onMaxSalaryChange } = this.props
    nextProps.salaryValue.min !== salaryValue.min && onMinSalaryChange(nextProps.salaryValue.min)
    nextProps.salaryValue.max !== salaryValue.max && onMaxSalaryChange(nextProps.salaryValue.max)
  }

  componentDidMount () {
    this.props.fetchCandidateDepartments()
    this.props.fetchCandidateLocations()
    this.props.fetchIndustries()
    this.props.fetchDepartments()
    this.props.fetchCities()
    this.props.fetchCurrencies()
  }

  render () {
    const { t, handleSubmit } = this.props
    return (
      <Form onSubmit={handleSubmit} className='complete-profile-preferences-mobile'>
        <div>
          <Field
            component={Input}
            name='jobType'
            type='hidden'
          />
          <RadioWrapper
            legend={t('forms.jobType.label')}
            hasError={this.props.hasJobTypeError}
            error={this.props.errorMessage}
          >
            <Field
              className='step_basic__radio_button'
              component={Checkbox}
              text={t('common.jobType.internship')}
              normalize={v => !!v}
              name='interestedInInternship'
            />
            <Field
              className='step_basic__radio_button'
              component={Checkbox}
              text={t('common.jobType.graduate_job')}
              normalize={v => !!v}
              name='interestedInGraduateJob'
            />
            <Field
              className='step_basic__radio_button'
              component={Checkbox}
              text={t('common.jobType.volunteering')}
              normalize={v => !!v}
              name='interestedInVolunteering'
            />
            <Field
              className='step_basic__radio_button'
              component={Checkbox}
              text={t('common.jobType.event_promotion')}
              normalize={v => !!v}
              name='interestedInEventPromotion'
            />
          </RadioWrapper>
        </div>
        <div className='complete-profile-preferences-mobile__field'>
          <Field
            component={GeosuggestMultiSelect}
            options={this.props.cities}
            label={t('forms.preferredLocation.label')}
            placeholder={t('forms.preferredLocation.placeholder')}
            description={t('common.selectUpToThree')}
            name='preferredLocations'
            suggestTypes={['(cities)']}
            maxItemsCount={MAX_JOB_PREFERENCES_COUNT}
          />
        </div>
        <div className='complete-profile-preferences-mobile__field'>
          <Field
            component={MultiSelect}
            options={this.props.departments}
            description={t('common.selectUpToThree')}
            label={t('forms.preferredDepartment.label')}
            placeholder={t('forms.preferredDepartment.placeholder')}
            name='departments'
            maxItemsCount={MAX_JOB_PREFERENCES_COUNT}
            withIcon
          />
        </div>
        <div className='complete-profile-preferences-mobile__field'>
          <Field
            component={MultiSelect}
            options={this.props.industries}
            description={t('common.selectUpToThree')}
            label={t('forms.preferredIndustry.label')}
            placeholder={t('forms.preferredIndustry.placeholder')}
            name='industries'
            maxItemsCount={MAX_JOB_PREFERENCES_COUNT}
            withIcon
          />
        </div>
        <div className='complete-profile-preferences-mobile__field'>
          <div className='complete-profile-preferences__field-salary-wrapper'>
            <div className='form__group'>
              <Label>{t('profile.preferences.sections.salaryExpectations.label')}</Label>
              <div className='complete-profile-preferences__field-salary-description'>{t('profile.preferences.sections.salaryExpectations.average')}</div>
            </div>
          </div>
          <div className='form__group complete-profile-preferences-mobile__field'>
            <Field
              label={t('forms.currency.label')}
              component={Select}
              options={this.props.currencies}
              placeholder={t('forms.currency.placeholder')}
              name='currencyId'
              withIcon
            />
          </div>
          <div>
            <SalaryExpectationFields {...this.props} />
          </div>
        </div>
      </Form>
    )
  }
}

export default withTranslations(CompleteProfilePreferencesForm)



