import React from 'react'
import { Title } from 'Shared/components/FormElements'
import { Trans } from 'react-i18next'
import { t } from 'i18next'
import './SignupCancelView.scss'

function SignupCancelView (props) {
  return (
    <div className='signup-cancel__container'>
      <Title text={t('authorization.signupCancelTitle')} />
      <div className='signup-cancel__description'>
        {t('authorization.signupCancelDescripton.info')}
        <br />
        <br />
        {t('authorization.signupCancelDescripton.farewell')}
        <br />
        <br />
        {t('authorization.signupCancelDescripton.signature')}
      </div>
      <div className='signup-cancel__questions-section'>
        <div className='signup-cancel__questions-section-title'>{t('authorization.signupCancelDescripton.anyQuestions')}</div>
        <Trans i18nKey='authorization.signupThanksDescription.contact' parent='div' className='signup-cancel__questions-section-description'>
          Contact us at <a href={`mailto:support@oliv.com`} className='signup-cancel__hyperlink'>support@oliv.com</a>
        </Trans>
      </div>
    </div>
  )
}

export default SignupCancelView



