import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import Loader from 'Shared/components/Loader/Loader'
import JobOffersService from 'Shared/services/JobOffersService'
import { t } from 'i18next'
import './InitChatView.scss'

const jobOffersService = new JobOffersService()

class InitChatView extends Component {
  async componentDidMount () {
    const { pendingConversationId } = this.props.match.params
    const { data } = await jobOffersService.createChannelFromInvitation(pendingConversationId)

    this.props.history.push(`/candidate/chat/${data.sid}`)
  }

  render () {
    return (
      <div className='init-chat-view'>
        <Loader description={t('chat.settingUp')} />
      </div>
    )
  }
}

export default withRouter(InitChatView)



