import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded, hasSubmitFailed } from 'redux-form'
import CareerPreferencesView from 'Candidate/views/CareerPreferencesView'
import { formName } from 'Candidate/connectors/Forms/CompleteProfile/Preferences/CompleteProfilePreferencesFormConnector'
import { getUserRole } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  submitCareerPreferencesSucceeded: hasSubmitSucceeded(formName)(state),
  submitCareerPreferencesFailed: hasSubmitFailed(formName)(state),
  userRole: getUserRole(state)
})

const mapDispatchToProps = dispatch => ({
  submitCareerPreferences: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(CareerPreferencesView)



