import React, { Component } from 'react'
import { Title } from 'Shared/components/FormElements'
import { Trans } from 'react-i18next'
import { withRouter } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import './SignupConfirmEmailView.scss'

class SignupConfirmEmailView extends Component {
  render () {
    const { t, location: { state: { email } } } = this.props

    return (
      <div className='signup-confirm-email__container'>
        <Title text={t('authorization.signupThanksTitle')} />
        <Trans i18nKey='authorization.signupThanksSubtitle' parent='div' className='signup-confirm-email__subtitle'>
          We just need to confirm your registration before getting started. Go to your inbox to <strong className='signup-confirm-email__subtitle-strong'>confirm your email.</strong>
        </Trans>
        <div className='signup-confirm-email__section'>
          <div className='signup-confirm-email__question'>{t('authorization.signupThanksDescription.didntReceiveEmail')}</div>
          <ol>
            <Trans i18nKey='authorization.signupThanksDescription.correctEmail' parent='li' className='signup-confirm-email__text'>
              Is <a href={`mailto:${email}`} className='signup-confirm-email__hyperlink'>{email}</a> your correct email?
            </Trans>
            <li className='signup-confirm-email__text'>{t('authorization.signupThanksDescription.checkSpam')}</li>
          </ol>
        </div>
        <div className='signup-confirm-email__section'>
          <div className='signup-confirm-email__question'>{t('authorization.signupThanksDescription.havingTrouble')}</div>
          <Trans i18nKey='authorization.signupThanksDescription.contact' parent='div' className='signup-confirm-email__text'>
            Contact us at <a href={`mailto:support@oliv.com`} className='signup-confirm-email__hyperlink'>support@oliv.com</a>
          </Trans>
        </div>
      </div>
    )
  }
}

export default withRouter(withTranslations(SignupConfirmEmailView))



