import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import * as R from 'ramda'

export const getOrganisationInvites = state => state.organisationInvites.entries

export const organisationInvitesList = createSelector(
  getOrganisationInvites,
  invites => transformToArray(invites)
)

export const getOrganisationInviteById = createSelector(
  getOrganisationInvites,
  (state, organisationInviteId) => organisationInviteId,
  (invites, organisationInviteId) => R.propOr({}, organisationInviteId, invites)
)



