import React from 'react'
import CareerPreferencesViewMobile from './CareerPreferencesView.mobile'
import CareerPreferencesViewWeb from './CareerPreferencesView'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CareerPreferencesView = props => props.isMobile ? <CareerPreferencesViewMobile {...props} /> : <CareerPreferencesViewWeb {...props} />

export default withDeviceWidthDetector(CareerPreferencesView)



